//该文件专门用于创建整个应用的路由器
import VueRouter from 'vue-router'
//引入组件
import About from '../pages/About'
import Home from '../pages/Home'
import News from '../pages/News'
import Message from '../pages/Message'
import Detail from '../pages/Detail'



//创建一个路由器
const router = new VueRouter({
    mode:'history',
    routes: [
        {
            name: 'guanyu',
            path: '/about',
            component: About,
            meta: { title: '关于', isAuth: true  }
        },
        {
            name: 'zhuye',
            path: '/home',
            component: Home,
            meta: { title: '主页' },
            children: [
                {
                    name: 'xinwen',
                    path: 'news',
                    component: News,
                    meta: { title: '新闻', isAuth: true },
                    // beforeEnter: (to, from, next) => {
                    //     //函数体
                    //      if(to.meta.isAuth)//判断是否需要鉴权
                    //      {
                    //         if(localStorage.getItem('school')==='atguigu'){
                    //             next()
                    //         }else{
                    //             alert('没有权限！')
                    //         }
                    //      }else{
                    //         next()
                    //      }
                    // }

                },
                {
                    name: 'xiaoxi',
                    path: 'message',
                    component: Message,
                    meta: { title: '消息', isAuth: true },
                    children: [
                        {
                            name: 'xiangqing',
                            // (下面第二种)
                            // path:'detail/:id/:title',
                            // (下面第三种)
                            path: 'detail',
                            component: Detail,
                            meta: { title: '详情', isAuth: true },

                            //props的第一种写法。值为对象,该对象中所有的key-value都会以props的形式传给Detail组件
                            // props:{a:1,b:'hello'}

                            //props的第二种写法。值为布尔值,若布尔值为真，就会把该路由收到的所有params参数,以props的形式传给Detail组件
                            // props:true

                            //props的第三种写法。值为函数,若布尔值为真，就会把该路由收到的所有params参数,以props的形式传给Detail组件
                            props($route) {
                                return { id: $route.query.id, title: $route.query.title }
                            }
                            // props({query}){
                            //     return {id:query.id,title:query.title}
                            // }
                            // props({query:{id,title}}){
                            //     return {id,title}
                            // }
                        }
                    ]
                }
            ]
        }

    ]
})
