//         next()
//     }else{
//         alert('没有权限！')
//     }
//  }else{
//     next()
//  }
// })
//全局后置路由守卫--初始化的时候被调用、每次路由切换之后被调用
router.afterEach((to,from,next)=>{
 //函数体
 console.log('后置路由守卫',to,from)
 document.title= to.meta.title  || 'xxf系统'
})

export default router