import axios from "axios"
import { nanoid } from "nanoid"

//home.Message功能相关的配置
export default{
    namespaced:true,
    actions:{
        ShowMessage(context){
            //console.log('开启了一个定时器')
            setInterval(() => {
                axios.get('https://api.uixsj.cn/hitokoto/get?type=social').then(
                    Response=>{
                        console.log('请求成功了！')
                        context.commit('ADD_MESSAGE',Response.data)
                    },
                    error=>{
                        alert(error.message)
                    }
                )
            }, 15000)
        }
    },
    mutations:{
        ADD_MESSAGE(state,value){
            state.BannerMessage=value
        }
    },
    state:{
        BannerMessage:'一句话爱上你'
    },
    getters:{
    }
}