import axios from "axios"
import { nanoid } from "nanoid"

//home.Message功能相关的配置
export default{
    namespaced:true,
    actions:{
        ShowWord(context){
            axios.get('https://api.uixsj.cn/hitokoto/get?type=social').then(
                Response=>{
                    console.log('请求成功了！')
                    context.commit('ADD_MESSAGE',{id:nanoid(),title:Response.data})
                },
                error=>{
                    alert(error.message)
                }
            )

        }
    },
    mutations:{
        ADD_MESSAGE(state,value){
            state.messageList.unshift(value)
        }
    },
    state:{
        messageList:[]
    },
    getters:{
    }
}