<template>
    <div class="col-xs-offset-2 col-xs-8">
        <div class="page-header">
            <button @click="back">后退</button>
            <button @click="forward">前进</button>
            <button @click="test">go</button><!--可前进可后退-->
            <h2 @click="ShowMessage">Vue Router Demo</h2>
            <!-- <span>
                <select v-model="n">
                    <option value="1">1</option>
                    <option value="1">2</option>
                    <option value="1">3</option>
                </select>
            </span> -->
            <div>{{BannerMessage}}</div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
export default {
    name: 'Banner',
    computed:{
        ...mapState('bannerMes',['BannerMessage'])
    },
    methods:{
        ...mapActions('bannerMes',['ShowMessage']),
        back(){
            this.$router.back()
        },
        forward(){
            this.$router.forward()
        },
        test(){
            this.$router.go(-2)
        }
    },
    mounted(){
        this.ShowMessage()
    }
}
</script>
