<template>
    <div>
        <button @click="ShowWord">随机显示句子</button>
        <ul>
            <li v-for="m in messageList" :key="m.id">
                <!-- 跳转路由并携带params参数，to的字符串写法 -->
                <!-- <router-link :to="`/home/message/detail/${m.id}/${m.title}`">{{m.title}}</router-link>&nbsp;&nbsp; -->

                <!-- 跳转路由并携带params参数，to的对象写法 -->
                <router-link :to="{
                    // path:'/home/message/detail',  //不允许使用path
                    name:'xiangqing',
                    query:{
                        id:m.id,
                        title:m.title
                    }
                }">
                    {{m.title}}
                </router-link>
                <button @click="pushShow(m)">push查看</button>
                <button @click="replaceShow(m)">replace查看</button>
            </li>
        </ul>
        <hr>
        <router-view></router-view>
    </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
export default {
    name: 'Message',
    computed:{
        ...mapState('message',['messageList'])
    },
    methods:{
        ...mapActions('message',['ShowWord']),
        pushShow(m){
            // console.log(this.$router)
            this.$router.push({
                name:'xiangqing',
                query:{
                    id:m.id,
                    title:m.title
                }
            })
        },
        replaceShow(m){
            this.$router.replace({
                name:'xiangqing',
                query:{
                    id:m.id,
                    title:m.title
                }
            })
        }
    },
    mounted(){
        //console.log('开启了一个定时器')
        // setInterval(() => {
        //     this.ShowWord()
        // }, 2000)
    },
    beforeDestroy(){
        console.log('mes被销毁了')
    }
}
</script>

